"use client";

import { useState, useRef } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { X, FileText } from "lucide-react";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog";
import { useRouter } from "next/navigation";
import InsuranceInfoForm from "./insurance-info-form";

type WizardStep = {
  id: number;
  title: string;
  component: React.ReactNode;
};

export default function DamageDeclarationWizard() {
  const router = useRouter();
  const [currentStep, setCurrentStep] = useState(1);
  const [showExitDialog, setShowExitDialog] = useState(false);
  const [formData, setFormData] = useState<Record<string, any>>({});
  const formRef = useRef<HTMLFormElement>(null);

  const steps: WizardStep[] = [
    {
      id: 1,
      title: "اطلاعات بیمه گذار",
      component: (
        <InsuranceInfoForm
          ref={formRef}
          onDataChange={(data: Record<string, any>) => handleFormDataChange(1, data)}
          initialData={formData[1] || {}}
        />
      ),
    },
    {
      id: 2,
      title: "دبیرخانه",
      component: <div className="p-8 text-center text-gray-500">محتوای مرحله دوم...</div>,
    },
    {
      id: 3,
      title: "بررسی",
      component: <div className="p-8 text-center text-gray-500">محتوای مرحله سوم...</div>,
    },
    {
      id: 4,
      title: "موارد آسیب دیده",
      component: <div className="p-8 text-center text-gray-500">محتوای مرحله چهارم...</div>,
    },
    {
      id: 5,
      title: "حادثه",
      component: <div className="p-8 text-center text-gray-500">محتوای مرحله پنجم...</div>,
    },
    {
      id: 6,
      title: "بیمه نامه",
      component: <div className="p-8 text-center text-gray-500">محتوای مرحله ششم...</div>,
    },
    {
      id: 7,
      title: "بیمه گذار",
      component: <div className="p-8 text-center text-gray-500">محتوای مرحله هفتم...</div>,
    },
  ];

  const handleFormDataChange = (stepId: number, data: Record<string, any>) => {
    setFormData((prev) => ({
      ...prev,
      [stepId]: data,
    }));
  };

  const handleStepClick = (stepId: number) => {
    // Save current form data before switching
    if (formRef.current) {
      const currentFormData = new FormData(formRef.current);
      const data: Record<string, any> = {};
      for (const [key, value] of currentFormData.entries()) {
        data[key] = value;
      }
      handleFormDataChange(currentStep, data);
    }
    setCurrentStep(stepId);
  };

  const handleTemporarySave = () => {
    if (formRef.current) {
      const currentFormData = new FormData(formRef.current);
      const data: Record<string, any> = {};
      for (const [key, value] of currentFormData.entries()) {
        data[key] = value;
      }
      handleFormDataChange(currentStep, data);
      
      // Here you would typically save to localStorage or send to server
      localStorage.setItem('damageDeclarationDraft', JSON.stringify(formData));
      
      // Show success message
      console.log("فرم به صورت موقت ذخیره شد");
    }
  };

  const handleExit = () => {
    setShowExitDialog(true);
  };

  const handleExitConfirm = () => {
    // Submit form and go to main page
    console.log("فرم ارسال شد:", formData);
    router.push("/panel");
  };

  const handleExitCancel = () => {
    setShowExitDialog(false);
  };

  const getCurrentStepComponent = () => {
    const step = steps.find((s) => s.id === currentStep);
    return step?.component || null;
  };

  return (
    <div className="min-h-screen bg-gray-50 p-4">
      <Card className="mx-auto max-w-4xl">
        <CardHeader className="relative">
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-2">
              <FileText className="h-8 w-8 text-blue-600" />
              <CardTitle className="text-2xl text-blue-600">فرم اعلام خسارت</CardTitle>
            </div>
            <Button
              variant="ghost"
              size="icon"
              onClick={handleExit}
              className="text-gray-500 hover:text-gray-700"
            >
              <X className="h-5 w-5" />
            </Button>
          </div>

          {/* Step Navigation */}
          <div className="flex flex-wrap gap-2 mt-6">
            {steps.map((step) => (
              <button
                key={step.id}
                onClick={() => handleStepClick(step.id)}
                className={`px-4 py-2 text-sm rounded-lg transition-colors ${
                  currentStep === step.id
                    ? "bg-blue-600 text-white"
                    : "bg-gray-200 text-gray-700 hover:bg-gray-300"
                }`}
              >
                {step.title}
              </button>
            ))}
          </div>
        </CardHeader>

        <CardContent>
          {getCurrentStepComponent()}

          {/* Action Buttons */}
          <div className="flex justify-between mt-8 pt-6 border-t">
            <Button
              onClick={handleTemporarySave}
              variant="outline"
              className="bg-green-50 border-green-300 text-green-700 hover:bg-green-100"
            >
              ذخیره موقت
            </Button>

            <div className="flex gap-2">
              {currentStep > 1 && (
                <Button
                  onClick={() => handleStepClick(currentStep - 1)}
                  variant="outline"
                >
                  مرحله قبل
                </Button>
              )}
              {currentStep < steps.length && (
                <Button
                  onClick={() => handleStepClick(currentStep + 1)}
                  className="bg-blue-600 hover:bg-blue-700"
                >
                  مرحله بعد
                </Button>
              )}
              {currentStep === steps.length && (
                <Button
                  onClick={handleExitConfirm}
                  className="bg-green-600 hover:bg-green-700"
                >
                  ارسال نهایی
                </Button>
              )}
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Exit Confirmation Dialog */}
      <Dialog open={showExitDialog} onOpenChange={setShowExitDialog}>
        <DialogContent className="sm:max-w-md">
          <DialogHeader>
            <DialogTitle>خروج از فرم</DialogTitle>
            <DialogDescription>
              آیا می‌خواهید فرم را ارسال کرده و به صفحه اصلی بروید؟
            </DialogDescription>
          </DialogHeader>
          <DialogFooter className="flex gap-2">
            <Button variant="outline" onClick={handleExitCancel}>
              خیر
            </Button>
            <Button onClick={handleExitConfirm} className="bg-red-600 hover:bg-red-700">
              بله، ارسال و خروج
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </div>
  );
}