"use client";

import { useState, forwardRef, useImperativeHandle, useEffect } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";

interface InsuranceInfoFormProps {
  onDataChange: (data: Record<string, any>) => void;
  initialData?: Record<string, any>;
}

const InsuranceInfoForm = forwardRef<HTMLFormElement, InsuranceInfoFormProps>(
  ({ onDataChange, initialData = {} }, ref) => {
    const [formData, setFormData] = useState({
      policyholderMobile: initialData.policyholderMobile || "",
      nationalCode: initialData.nationalCode || "",
      birthYear: initialData.birthYear || "",
      birthMonth: initialData.birthMonth || "",
      birthDay: initialData.birthDay || "",
      registrationPhone: initialData.registrationPhone || "",
      ...initialData,
    });

    const currentYear = new Date().getFullYear();
    const persianYear = currentYear - 621; // Convert to Persian/Jalali year approximately

    // Generate year options (last 100 years in Persian calendar)
    const yearOptions = Array.from({ length: 100 }, (_, i) => persianYear - i);
    
    // Persian month names
    const monthOptions = [
      { value: "1", label: "فروردین" },
      { value: "2", label: "اردیبهشت" },
      { value: "3", label: "خرداد" },
      { value: "4", label: "تیر" },
      { value: "5", label: "مرداد" },
      { value: "6", label: "شهریور" },
      { value: "7", label: "مهر" },
      { value: "8", label: "آبان" },
      { value: "9", label: "آذر" },
      { value: "10", label: "دی" },
      { value: "11", label: "بهمن" },
      { value: "12", label: "اسفند" },
    ];

    // Day options (1-31)
    const dayOptions = Array.from({ length: 31 }, (_, i) => i + 1);

    useImperativeHandle(ref, () => {
      const form = document.createElement("form");
      
      // Create hidden inputs for form data
      Object.entries(formData).forEach(([key, value]) => {
        const input = document.createElement("input");
        input.type = "hidden";
        input.name = key;
        input.value = value as string;
        form.appendChild(input);
      });
      
      return form;
    });

    const handleInputChange = (field: string, value: string) => {
      const newData = { ...formData, [field]: value };
      setFormData(newData);
      onDataChange(newData);
    };

    useEffect(() => {
      onDataChange(formData);
    }, []);

    return (
      <div className="space-y-6">
        <Card>
          <CardHeader>
            <CardTitle className="text-lg text-blue-600">اطلاعات بیمه گذار</CardTitle>
          </CardHeader>
          <CardContent className="space-y-6">
            {/* Policyholder Mobile Number */}
            <div className="space-y-2">
              <Label htmlFor="policyholderMobile" className="text-sm font-medium">
                شماره موبایل بیمه گذار <span className="text-red-500">*</span>
              </Label>
              <Input
                id="policyholderMobile"
                type="tel"
                placeholder="۰۹۱۲۱۲۳۴۵۶۷۸"
                value={formData.policyholderMobile}
                onChange={(e) => handleInputChange("policyholderMobile", e.target.value)}
                className="bg-green-50 border-green-300"
                dir="ltr"
              />
            </div>

            {/* National Code */}
            <div className="space-y-2">
              <Label htmlFor="nationalCode" className="text-sm font-medium">
                کد ملی <span className="text-red-500">*</span>
              </Label>
              <Input
                id="nationalCode"
                type="text"
                placeholder="کد ملی ده رقمی"
                value={formData.nationalCode}
                onChange={(e) => handleInputChange("nationalCode", e.target.value)}
                maxLength={10}
                dir="ltr"
              />
            </div>

            {/* Birth Date */}
            <div className="space-y-2">
              <Label className="text-sm font-medium">
                تلفن ثبت بیمه نامه (اختیاری)
              </Label>
              <Input
                id="registrationPhone"
                type="tel"
                placeholder="تلفن ثابت یا کد شهر"
                value={formData.registrationPhone}
                onChange={(e) => handleInputChange("registrationPhone", e.target.value)}
                dir="ltr"
              />
            </div>

            {/* Birth Date */}
            <div className="space-y-2">
              <Label className="text-sm font-medium">
                تاریخ تولد <span className="text-red-500">*</span>
              </Label>
              <div className="grid grid-cols-3 gap-3">
                {/* Day */}
                <div>
                  <Select 
                    value={formData.birthDay} 
                    onValueChange={(value) => handleInputChange("birthDay", value)}
                  >
                    <SelectTrigger>
                      <SelectValue placeholder="روز" />
                    </SelectTrigger>
                    <SelectContent>
                      {dayOptions.map((day) => (
                        <SelectItem key={day} value={day.toString()}>
                          {day}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>

                {/* Month */}
                <div>
                  <Select 
                    value={formData.birthMonth} 
                    onValueChange={(value) => handleInputChange("birthMonth", value)}
                  >
                    <SelectTrigger>
                      <SelectValue placeholder="ماه" />
                    </SelectTrigger>
                    <SelectContent>
                      {monthOptions.map((month) => (
                        <SelectItem key={month.value} value={month.value}>
                          {month.label}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>

                {/* Year */}
                <div>
                  <Select 
                    value={formData.birthYear} 
                    onValueChange={(value) => handleInputChange("birthYear", value)}
                  >
                    <SelectTrigger>
                      <SelectValue placeholder="سال" />
                    </SelectTrigger>
                    <SelectContent>
                      {yearOptions.map((year) => (
                        <SelectItem key={year} value={year.toString()}>
                          {year}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>
              </div>
            </div>

            {/* Identity Document Registration */}
            <div className="bg-blue-50 p-4 rounded-lg">
              <Button 
                type="button" 
                className="w-full bg-blue-600 hover:bg-blue-700 text-white"
                onClick={() => {
                  // Here you would implement the identity verification process
                  console.log("استعلام هویت...");
                }}
              >
                ← استعلام هویت
              </Button>
            </div>
          </CardContent>
        </Card>
      </div>
    );
  }
);

InsuranceInfoForm.displayName = "InsuranceInfoForm";

export default InsuranceInfoForm;